.container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.text {
    margin-top: 180px;
    margin-bottom: 20px;
    text-align: center;
    font-size: 48px;
    font-weight: 500;
    letter-spacing: 1px;
}

.link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 160px;
    height: 50px;
    font-size: 18px;
    text-align: center;
    text-transform: uppercase;
    border: 0px;
    border-radius: 20px;
    background-color: #3498db;
    color: white;
    box-shadow: none;
    transition: all 250ms ease-in-out;
}

.link:hover,
.link:focus {
    background-color: #39b0ff;
    box-shadow: 0px 4px 4px #00000026, inset 0px 0px 0px 0px #0000004d;
}